
.navbar {
    /* padding: 30px; */
    overflow: hidden;
    /* display: flex; */
    /* margin: 0 auto; */
    background-color: white;
    border-bottom: 1px solid lightgray;
}

.navbar a {
    float: left;
    color: black;
    text-align: center;
    /* padding: 14px 16px; */
    padding: 20px 30px;
    text-decoration: none;
    font-size: 17px;
    /* background-color: lightblue; */
    border-radius: 5px;
}

.navbar a:hover {
    background-color: #FF9933;
    color: black;
}

.navbar a.active {
    background-color: #138808;
    color: white;
}

.navbar-right {
    float: right;
}

.navbar-right a:hover {
    background-color: #138808;
    color: white;
}

.div-two-column {
    display: flex;
    flex-direction: row;
    padding: 30px;
}

.left-column {
    padding-right: 50px;
}


